.wcf-ghp-item {
  display: flex;
  flex-direction: column;
}

.wcf-ghp-cat-list li a {
  display: inline-block;
  padding: 5px 10px;
  background: #1e1e1e;
  color: #fff;
}

.wcf-ghp-cat-list {
  display: flex;
  gap: 5px;
}

.wcf-ghp-meta {
  display: flex;
  align-items: center;
}

.wcf-ghp-divider {
  height: 10px;
  width: 30px;
  background: #f00;
  margin-left: 10px;
  margin-right: 10px;
}

.wcf-ghp-avatar {
  height: 30px;
  width: 30px;
  border-radius: 50px;
}

.wcf-grid-hover-posts svg {
  width: 1em;
  height: 1em;
}

.wcf-grid-hover-posts {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.wcf-grid-hover-posts .wcf-ghp-item {
  border-right: 1px solid rgba(255, 255, 255, 0.3019607843);
  padding: 300px 30px 30px;
  overflow: hidden;
}

.wcf-grid-hover-posts .title {
  color: #fff;
  transition: all 0.3s;
  transform: translateY(25px);
}

.wcf-grid-hover-posts .service-btn {
  fill: #fff;
  color: #fff;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s;
  position: relative;
  transform: translateY(25px);
  opacity: 0;
  visibility: hidden;
}

.wcf-grid-hover-posts .wcf-ghp-item:hover .title {
  transform: translateY(0);
}

.wcf-grid-hover-posts .wcf-ghp-item:hover .service-btn {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.wcf-grid-hover-posts .image {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -1;
  transition: all 0.5s;
  opacity: 0;
}

.wcf-grid-hover-posts .image::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(18, 18, 18, 0.2509803922);
}

.wcf-grid-hover-posts .image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.wcf-grid-hover-posts .wcf-ghp-item:first-child .image {
  opacity: 1;
}

.wcf-grid-hover-posts .wcf-ghp-item:hover .image {
  opacity: 1;
}

/* Responsive */
@media (max-width: 1200px) {
  .wcf-grid-hover-posts .wcf-ghp-item {
    position: relative;
  }
  .wcf-grid-hover-posts .image {
    opacity: 1;
  }
  .wcf-grid-hover-posts .service-btn,
  .wcf-grid-hover-posts .title {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }
  .wcf-grid-hover-posts .wcf-ghp-item:hover .title {
    transform: translateY(0) !important;
  }
}